.modalbutton {
  background: #2e6cd1;
  padding: 10px 10px;
  color: white;
  border: 0;
  margin: 5px 2px;
}
.button {
  float: right;
  background: #2e6cd1;
  padding: 1em 2em;
  color: white;
  border: 0;
}
.button:hover {
  background: #333;
}
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: scroll;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-animation-name: modalAni;
          animation-name: modalAni;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}
.modalForm {
  background: #f4f4f4;
  margin: 10% auto;
  width: 60%;
}
.modalHeader {
  width: 100%;
  background: coral;
  text-align: center;
  overflow: hidden;
}
.forms {
  width: 100%;
  overflow: hidden;
  padding: 0px 10px;
}
input[type=text] {
  width: 95%;
  padding: 12px 20px;
  margin: 8px 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.closeBtn {
  background: transparent;
  font-size: 30px;
  float: right;
  width: 15%;
}
.closeBtn:hover,
.closeBtn:focus {
  color: #746c6c;
  cursor: pointer;
}
.modalFooter {
  background: coral;
  width: 100%;
  text-align: center;
  overflow: hidden;
  height: 50px;
}
.addBtn {
  font-size: 20px;
  text-align: center;
  padding: 10px;
}
.addBtn:hover,
.addBtn:focus,
.modalFooter:hover {
  background: rgb(172, 26, 26);
  color: #ffffff;
  cursor: pointer;
}

/*main content*/
table,
tr {
  width: 100%;
  border: 1px solid black;
  text-align: center;
  border-collapse: collapse;
  padding: 1px;
  background: #f4f4f4;
}
th {
  background: coral;
}
th,
td {
  border: 1px solid black;
  padding: 1px;
}
.mainHeader {
  padding: 10px 5px 0px 0px;
  margin: 0;
}
@-webkit-keyframes modalAni {
from {
    opacity: 0
}
to {
    opacity: 1
}
}
@keyframes modalAni {
from {
    opacity: 0
}
to {
    opacity: 1
}
}

